.App {
  text-align: center;
  background: #141414;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  filter: drop-shadow(0 0 8px #ff2be6) drop-shadow(0 0 16px #00eaff);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border: 2px solid #00eaff;
  box-shadow: 0 0 8px #00eaff, 0 0 16px #ff2be6;
  animation: neon-flicker 2s infinite alternate;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
  border-bottom: 2px solid #00eaff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes neon-flicker {
  0% { box-shadow: 0 0 8px #39ff14, 0 0 16px #39ff14; }
  50% { box-shadow: 0 0 24px #39ff14, 0 0 32px #39ff14; }
  100% { box-shadow: 0 0 8px #39ff14, 0 0 16px #39ff14; }
}

.text-neon-green {
  color: #39ff14 !important;
  text-shadow: 0 0 6px #39ff14, 0 0 12px #39ff14;
}
.bg-neon-green {
  background: #39ff14;
}
.neon-border {
  border: 2px solid #39ff14;
  box-shadow: 0 0 8px #39ff14, 0 0 16px #39ff14;
}
.neon-shadow {
  box-shadow: 0 0 24px #39ff14, 0 0 12px #39ff14;
}

.glitch {
  position: relative;
  color: #39ff14;
  text-shadow: 0 0 6px #39ff14, 0 0 12px #39ff14, 2px 0 #222, -2px 0 #222;
  animation: glitch 1s infinite linear alternate-reverse;
}
@keyframes glitch {
  0% { text-shadow: 0 0 6px #39ff14, 0 0 12px #39ff14, 2px 0 #222, -2px 0 #222; }
  20% { text-shadow: 0 0 12px #39ff14, -2px -2px #222, 2px 2px #222; }
  40% { text-shadow: 0 0 6px #39ff14, 2px 2px #222, -2px -2px #222; }
  60% { text-shadow: 0 0 12px #39ff14, -2px 2px #222, 2px -2px #222; }
  80% { text-shadow: 0 0 6px #39ff14, 2px -2px #222, -2px 2px #222; }
  100% { text-shadow: 0 0 12px #39ff14, -2px 0 #222, 2px 0 #222; }
}

.typewriter {
  overflow: hidden;
  border-right: .15em solid #39ff14;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .15em;
  animation: typing 2.5s steps(40, end), blink-caret .75s step-end infinite;
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #39ff14; }
}

.cyber-btn {
  background: var(--btn-bg, #111);
  color: var(--btn-color, #39ff14);
  font-weight: bold;
  border: 2px solid var(--btn-border, #39ff14);
  border-radius: 8px;
  box-shadow: 0 0 16px var(--btn-shadow, #39ff14), 0 0 32px var(--btn-shadow2, #39ff14);
  padding: 0.75em 2em;
  transition: transform 0.1s, box-shadow 0.1s, background 0.2s;
  cursor: pointer;
  position: relative;
  z-index: 1;
  text-align: center;
}

.cyber-btn:hover, .cyber-btn:focus, .cyber-btn:active {
  background: linear-gradient(90deg, #ff00c8 0%, #00fff7 100%) !important;
  color: #000 !important;
  border: 2px solid #00fff7;
  box-shadow: 0 0 28px #00fff7, 0 0 38px #ff00c8;
  outline: none;
}

.leaderboard-btn {
  background: linear-gradient(90deg, #00fff7 0%, #ff00c8 100%) !important;
  color: #000 !important;
  font-weight: bold;
  border: 2px solid #ff00c8;
  border-radius: 10px;
  box-shadow: 0 0 18px #ff00c8, 0 0 28px #00fff7;
  text-shadow: 0 0 4px #00fff7, 0 0 8px #ff00c8;
  animation: leaderboard-flicker 1.2s infinite alternate;
  transition: transform 0.12s, box-shadow 0.2s;
  position: fixed;
  top: 1.2rem;
  right: 2rem;
  z-index: 100;
  font-size: 1.12em;
  padding: 0.52em 1.5em;
  min-width: 100px;
  max-width: 170px;
  font-weight: bold;
  border-radius: 12px;
  box-shadow: 0 0 18px #ff00c8, 0 0 28px #00fff7;
}

@media (max-width: 700px) {
  .leaderboard-btn {
    top: max(0.7rem, env(safe-area-inset-top, 0.7rem));
    right: 0.7rem;
    font-size: 0.98em;
    padding: 0.48em 0.8em;
    min-width: 70px;
    max-width: 120px;
    border-radius: 9px;
  }
}

@media (max-width: 700px) {
  .header-bar {
    margin: 2.2rem auto 1.2rem auto !important;
    padding: 0.7rem 0.7rem !important;
    font-size: 1.1rem !important;
    max-width: 95vw !important;
  }
}
@media (max-width: 700px) {
  .leaderboard-btn {
    top: 0.5rem;
    right: 0.5rem;
    left: auto;
    width: auto;
    min-width: 64px;
    max-width: 120px;
    font-size: 0.92em;
    padding: 0.45em 0.8em;
    border-radius: 9px;
  }
}

@media (max-width: 700px) {
  .leaderboard-btn {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
    bottom: max(1.2rem, env(safe-area-inset-bottom, 1.2rem));
    width: 90vw;
    min-width: 0;
    max-width: 400px;
    font-size: 1.05em;
    padding: 0.9em 0;
    border-radius: 18px;
  }
}

.leaderboard-btn:hover {
  transform: scale(1.08) rotate(-1deg);
  box-shadow: 0 0 64px #ff00c8, 0 0 96px #00fff7;
  animation: leaderboard-flicker 0.3s alternate infinite;
  filter: brightness(1.15);
}
@keyframes leaderboard-flicker {
  0%, 100% { box-shadow: 0 0 32px #ff00c8, 0 0 64px #00fff7; }
  60% { box-shadow: 0 0 48px #ff00c8, 0 0 96px #00fff7; }
  80% { box-shadow: 0 0 16px #ff00c8, 0 0 32px #00fff7; }
}

.cyber-btn:hover {
  transform: scale(1.07) rotate(-1deg);
  box-shadow: 0 0 32px var(--btn-shadow, #39ff14), 0 0 64px var(--btn-shadow2, #39ff14);
  background: var(--btn-bg, #181818);
}

/* CYBERPUNK / GLITCH / NEON / TERMINAL STYLES */
@keyframes neon-flicker {
  0%, 100% { opacity: 1; }
  90% { opacity: 0.8; }
  95% { opacity: 0.4; }
}
@keyframes glitch {
  0% { text-shadow: 2px 0 #ff00c8, -2px 0 #00eaff; }
  20% { text-shadow: -2px 0 #ff00c8, 2px 0 #00eaff; }
  40% { text-shadow: 2px 2px #ff00c8, -2px -2px #00eaff; }
  60% { text-shadow: -2px 2px #ff00c8, 2px -2px #00eaff; }
  80% { text-shadow: 0 0 10px #fff, 0 0 20px #ff00c8; }
  100% { text-shadow: 2px 0 #ff00c8, -2px 0 #00eaff; }
}
.glitch {
  animation: glitch 1.2s infinite alternate;
  color: #39ff14;
  font-family: 'Orbitron', 'Share Tech Mono', monospace;
  letter-spacing: 1px;
}
.neon-border {
  border: 2px solid #00fff7;
  box-shadow: 0 0 16px #00fff7, 0 0 32px #ff00c8 inset;
  border-radius: 12px;
}
.cyber-btn {
  background: linear-gradient(90deg, #ff00c8 0%, #00fff7 100%);
  color: #000;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0 12px #ff00c8, 0 0 24px #00fff7;
  transition: transform 0.1s, box-shadow 0.2s;
}
.cyber-btn:hover {
  transform: translateX(2px) scale(1.04) rotate(-1deg);
  box-shadow: 0 0 32px #ff00c8, 0 0 48px #00fff7;
  animation: neon-flicker 0.3s alternate infinite;
}
.terminal-type {
  font-family: 'Share Tech Mono', monospace;
  color: #39ff14;
  background: #111;
  padding: 1rem 2rem;
  font-size: 1.2em;
  border-radius: 10px;
  margin-bottom: 24px;
  box-shadow: 0 0 16px #00fff7;
  width: fit-content;
  animation: neon-flicker 1.5s infinite alternate;
}
.blinking-cursor {
  animation: neon-flicker 1s infinite;
  color: #ff00c8;
}

.neon-input {
  background: rgba(10,10,10,0.85);
  color: #39ff14;
  border: 2px solid #39ff14;
  border-radius: 6px;
  box-shadow: 0 0 8px #39ff14;
  outline: none;
  font-family: 'Fira Mono', 'Consolas', 'Menlo', monospace;
  padding: 0.7em 1em;
  margin-bottom: 1em;
  transition: box-shadow 0.2s, border-color 0.2s;
}
.neon-input:focus {
  border-color: #39ff14;
  box-shadow: 0 0 18px #39ff14, 0 0 32px #39ff14;
  background: rgba(20,20,20,0.95);
}
.neon-input::placeholder {
  color: #39ff14;
  opacity: 0.7;
  font-style: italic;
}

/* Neon form background */
.neon-form {
  background: rgba(10,10,10,0.92);
  border: 2.5px solid #39ff14;
  border-radius: 12px;
  box-shadow: 0 0 32px #39ff14, 0 0 64px #39ff14;
  padding: 2em 2.5em 2em 2.5em;
  margin: 0 auto 2em auto;
  max-width: 350px;
  z-index: 2;
}

.cyber-btn:hover {
  transform: scale(1.05) rotate(-1deg);
  box-shadow: 0 0 24px #39ff14, 0 0 32px #39ff14;
  background: #191919;
}

.cyber-card {
  background: #181818 !important;
  border: 2px solid #39ff14;
  box-shadow: 0 0 12px #39ff14, 0 0 24px #39ff14;
  transition: 0.2s;
  color: #39ff14;
  text-align: center;
}
.cyber-card:hover {
  background: #222 !important;
  box-shadow: 0 0 32px #39ff14, 0 0 64px #39ff14;
}

.terminal-font {
  font-family: 'Fira Mono', 'Consolas', 'Menlo', monospace;
}

html, body {
  min-height: 100vh;
  width: 100vw;
  background: #111 !important;
  position: relative;
  overflow-x: hidden;
}

body::before {
  content: '';
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 0;
  pointer-events: none;
  opacity: 0.20;
  background: repeating-linear-gradient(120deg, #39ff14 0 2px, transparent 2px 8px),
    linear-gradient(90deg, #39ff14 0%, #111 50%, #39ff14 100%);
  background-size: 300% 300%, 200% 200%;
  animation: bg-glitch-move 3s infinite linear alternate, bg-glitch-shift 2.5s infinite steps(8);
}

body::after {
  content: '';
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 1;
  pointer-events: none;
  opacity: 0.09;
  background:
    repeating-linear-gradient(
      to bottom,
      rgba(57,255,20,0.06) 0px,
      rgba(57,255,20,0.06) 1px,
      transparent 1.5px,
      transparent 4px
    );
  animation: scanlines-move 1.5s linear infinite;
}

@keyframes scanlines-move {
  0% { background-position-y: 0; }
  100% { background-position-y: 4px; }
}

/* Digital noise overlay */
#noise-overlay {
  pointer-events: none;
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  opacity: 0.08;
  mix-blend-mode: screen;
}

@keyframes bg-glitch-move {
  0% { background-position: 0 0, 0 0; }
  100% { background-position: 100% 100%, 100% 0; }
}
@keyframes bg-glitch-shift {
  0%, 100% { filter: hue-rotate(0deg) brightness(1.1); }
  20% { filter: hue-rotate(30deg) brightness(1.2); }
  40% { filter: hue-rotate(-20deg) brightness(0.9); }
  60% { filter: hue-rotate(10deg) brightness(1.3); }
  80% { filter: hue-rotate(-40deg) brightness(1.0); }
}


::-webkit-scrollbar-thumb {
  background: #39ff14;
  border-radius: 10px;
}

.centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
